*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --dm-family: "DM Sans", sans-serif;
  --titan-family: "Titan One", sans-serif;
  --red: #d41443;
  --white: #fff;
  --pink: #ffa5ba;
  --black: #000;
  --beige: #f0d1a5;
  --green: #c2e297;
  --gray: #907e82;
  --background-color: #ffb8ca;
  --hover-buttons: #ecebeb;
  --icons-background: #fea5bb;
  --transition-hover: 250ms cubic-bezier(.4, 0, .2, 1);
  --transform-scale: scale(1.15);
  --transform-rotate: rotate(270deg);
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

* {
  box-sizing: border-box;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

body {
  font-family: var(--dm-family);
  font-size: 16px;
  font-weight: 500;
}

button:hover {
  cursor: pointer;
}

.header {
  background-color: var(--background-color);
  height: 58px;
}

.header-container {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.nav {
  display: none;
}

.nav__link {
  color: #fff;
  font-family: var(--dm-family);
  z-index: 1;
  font-weight: 700;
  line-height: 20.83px;
}

.nav__link:hover, .nav__link:focus {
  color: var(--red);
  background-color: rgba(0, 0, 0, 0) !important;
}

.logo-header {
  width: 199px;
  height: 50px;
  transition: transform var(--transition-hover);
  margin-top: 8px;
  margin-left: 5px;
}

.logo-header:hover {
  transform: var(--transform-scale);
}

.header-buttons {
  display: block;
}

.header-buttons .button--big {
  display: none;
}

.header-buttons .menu-toggle {
  width: 20px;
  height: 20px;
  top: 16px;
  right: 20px;
}

.buy-now {
  justify-content: center;
  display: flex;
}

.button__chevron {
  width: 4px;
  height: 8px;
  stroke: var(--red);
  margin-left: 12px;
}

.buyNowToggle-btn {
  width: 209px;
  height: 44px;
  color: var(--red);
  transition: transform var(--transition-hover);
  background-color: #fff;
  border: 2px solid #fff;
  border-radius: 22px;
  margin-top: 40px;
  font-size: 16px;
  font-weight: 700;
}

.header-buttons .button--big:hover, .header-buttons .button--big:focus {
  background-color: var(--hover-buttons);
  border-color: var(--hover-buttons);
  transform: var(--transform-scale);
}

.container-menu-mobile {
  height: 796px;
  justify-content: flex-end;
  display: flex;
}

.container-navbar .logo {
  position: absolute;
  top: 24px;
  left: 16px;
}

.js-open-menu {
  position: absolute;
  top: 24px;
  right: 16px;
}

.menu-toggle {
  color: var(--red);
  min-height: 40px;
  min-width: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu-container {
  width: 100vw;
  height: 502px;
  z-index: 100;
  background-color: #e17992;
  padding: 32px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  color: #000;
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  flex-direction: column;
  gap: 40px;
  margin-top: 70px;
  padding: 0;
  list-style: none;
  display: flex;
}

.mobile-menu li {
  border-top: 1px solid rgba(255, 255, 255, .03);
}

.mobile-menu .link {
  font-family: var(--dm-family);
  letter-spacing: .02em;
  text-transform: capitalize;
  color: var(--white);
  gap: 40px;
  padding: 10px;
  font-size: 14px;
  font-weight: 700;
  line-height: 111%;
  text-decoration: none;
}

.mobile-menu .link:hover, .mobile-menu .link:focus {
  color: var(--red);
}

.close-btn {
  color: #fff;
  transition: transform var(--transitions);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  font-size: 30px;
  font-weight: 100;
  position: absolute;
  top: 20px;
  right: 20px;
}

.close-btn:hover {
  color: #000;
  cursor: pointer;
  transform: scale(1.5);
}

